import React, { PureComponent, Suspense } from "react";

const CircularFilter = React.lazy(() => import("./FilterType/CircularFilter"));
const RectangularFilter = React.lazy(() => import("./FilterType/RectangularFilter"));
const RoundEdgedFilter = React.lazy(() => import("./FilterType/RoundEdgedFilter"));
const SquareFilter = React.lazy(() => import("./FilterType/SquareFilter"));
const TagFilters = React.lazy(() => import("./TagFilters"));

export default class Filters extends PureComponent {
  state = { webFiltersStatus: 0 }
  changeFilterStatus = (status) => {
    this.setState({ webFiltersStatus: status })
  }
  render() {
    const { type, webFilters } = this.props;
    const { webFiltersStatus } = this.state
    const N_filters = webFilters && webFilters.length > 1 ? true : false

    return (
      <div className="tb_filter_container">
        {N_filters ? type == 1 ? <Suspense fallback={<></>}><CircularFilter webFiltersStatus={webFiltersStatus} changeFilterStatus={this.changeFilterStatus} {...this.props} /></Suspense> : type == 4 ? <Suspense fallback={<></>}><RectangularFilter webFiltersStatus={webFiltersStatus} changeFilterStatus={this.changeFilterStatus} {...this.props} /></Suspense> : type == 2 ? <Suspense fallback={<></>}><RoundEdgedFilter webFiltersStatus={webFiltersStatus} changeFilterStatus={this.changeFilterStatus} {...this.props} /></Suspense> : type == 3 ? <Suspense fallback={<></>}><SquareFilter webFiltersStatus={webFiltersStatus} changeFilterStatus={this.changeFilterStatus} {...this.props} /></Suspense> : null : null}
        <Suspense fallback={<></>}><TagFilters N_filters={N_filters} {...this.props} webFiltersStatus={webFiltersStatus} changeFilterStatus={this.changeFilterStatus} /></Suspense>
      </div>
    );
  }
}
